} from '@/utils/other/commonFun.js'
import safari from '@/img/index/sfwzbg.png'

export default {
  data() {
    return {
      value: 3,
      reg: false,
      reg1: false,
      reg2: false,
      platform: '',
      tid: "",
      emptyData: false,
      data: [],
      options: [{
        name: '',
        icon: safari,
      },],
      local_url: ""
    }
  },
  mounted() {
    this.tid = this.$route.query.tid,
        this.qrcode();
    this.isMobile();
    down({
      tid: this.tid
    }).then(res => {
      this.emptyData = true;
      if (this.platform == "android") {
        this.data = res.apk;
      } else {
        this.data = res.ipa;
      }
    }).catch(error => {
      this.emptyData = false;
      console.log(error);
    })

  },
  methods: {
    totast() {
      Toast.fail(str);
    },
    isMobile() {
      let from = getPlatform();
      this.platform = from.platform;
      if (from.platform == "pc") {
        this.reg = true;
        // this.reg1 = false;
        // this.reg2 = false;
        // console.log("PC");
      }
      if (from.isWeixin) {
        this.reg = false;
        this.reg1 = true;
        this.reg2 = false;
      }
      if (from.isQQ) {
        this.reg = false;
        this.reg1 = true;
        this.reg2 = false;
      }
      if (from.terminal_platform == "ios") {
        if (!from.isSafari) {
          this.reg = false;
          this.reg1 = false;
          this.reg2 = true;
        } else {
          this.reg = false;
          this.reg1 = false;
          this.reg2 = false;
        }
      }
    },
    qrcode() {
      if (this.emptyData) {
        let qrcode = new QRCode('qrcode', {
          width: 200,
          height: 200,
          text: document.location.href // 
        })
      }
    },

  }
}
